import React, { useContext} from "react";
import { Link } from "react-router-dom";
import VersesContext from "../contexts/BibleContext";
import { CloseIcon } from "./AppIcons";
import BookCard from "./BookCard";

function Content() {
    const { books } = useContext(VersesContext);
    const tstmnts = ["STARI ZAVJET", "NOVI ZAVJET"]

    const TocCard = (tstmnt) => {
        return (
                <div className="toc-card" key={tstmnt}>
                    <div className="toc-card-header">
                        {tstmnt}
                    </div>
                    <div>
                        {
                            books.filter((book) => (
                                book.testament === tstmnt
                            ))
                            .map(book => (
                                <div key={book.index}>
                                    <Link to={`/book/${book.eng_short}`}>
                                        <BookCard shortName={book.hrv_short} fullName={book.name} />
                                    </Link>
                                </div>
                                ))
                        }
                    </div>
                </div>
            )
    }

    return (
        <div>
            <div className="close-page"><CloseIcon /></div>
            <div className="toc">
                {
                    tstmnts.map((tstmnt) => TocCard(tstmnt))
                }
            </div>
        </div>
    )
}

export default Content;